.profile_header {
    height: 175px;
    background-position: 0 center;
    background-size: cover;
    background-repeat: no-repeat;
    padding: 0 35px;

}

.profile_header__content {
    display: flex;
    flex-direction: row;
    align-items: center;
    height: 100%;
}

.profile__img {
    height: 84px;
    width: 84px;
}

.profile__info {
    width: 70px;
}

.profile__info h2 {
    color: #f2702c;
    font-size: 14px;
    margin: 0
}

.profile__info p {
    /*color: #9a9ca6;*/
    margin: 0;
    color: #fff;
    font-size: 8px;
}
